
@import 'style';

/****************************/
/* Layout Style             */
/****************************/
body{
    min-height:1px;
}
#container{
    position: relative;
    overflow: hidden;
    word-break: keep-all;

    >.inner{
        position: relative;
        max-width: 2560px;
        padding-left:$leftArea;
        margin: 0 auto;   
    }

    &:before{
        content:'';
        position: absolute;
        top:0;
        left:0;
        z-index: -1;
        width:100%;
        height:54.375rem;
        background-color: #f5f5f5;
    }
    &.type_white:before{
        top: 53rem;
        height:80rem;
        // height:72vw;
   
        @media (max-width:1280px) {
            height:73vw;    
        }
        @media (max-width:768px) {
            top: 45rem;
            height:95vw;    
        }
        @media (min-width:1920px) {
            height:70vw;    
        }
    }
    &.type_gray:before{
        top:0;
    }
    &.type_clear:before{
        display: none;
    }
    &.type_view{
        &:before{
            display: none;
        }
        >.inner{
            max-width:100%;
            padding-left:0;
        }
    }
    &.type_dark{
        background-color: #282828;
        &:before{
            display: none;
        }
        *{
            color:#fff;
        }
        header{
            .logo{
                background-image: url('../images/common/common-adcapsule-ci@2x-w.png');
            }

            .menu{
                background-color: transparent;
                .line{
                    background-color: #fff;
                    &:after,
                    &:before{
                        background-color: #fff;
                    }
                }
            }
        }
        .logo_dtp{
            display: none;
        }

    }
}


body.fix{
    overflow-y: hidden;
}

.monly{
    display:none;
}

@media (max-width:1024px) {
    #container{
        >.inner{
            padding-left:68px;
        }
    }
}

@media (max-width:480px) {
    #container{
        >.inner{
            padding-left:0;
        }

        &:before{
            height:420px;
        }
        &.type_view{
            >.inner{
                padding: 0;
            }
        }
        &.type_white:before{
            top: 14%;
            height:13%;
        }

        .inner_left{
            padding-left: 68px;
        }
    }
    .monly{
        display:block;
    }
}