
@import 'style';

footer {
   position: relative;
   padding: 0 0 0 $leftArea;

   .fbox{
      position: relative;
      padding: 50px 0;
      
      ul {
         position: relative;
         // height: 130px;
         @include clear;
         max-width: 82%;
         
         li{
            display: inline-block;
            padding-left: 25px;
            margin-right: 45px;
            margin-bottom: 15px;
            font-size: 14px;
            color: #3b3b3b;
            background-repeat: no-repeat;
            background-position: 0 center;

            &:last-child{
               margin: 0;
            }

            &.place{
               background-image: url('../images/common/footer-location-icon.png');
            }
            &.site{
               background-image: url('../images/common/footer-mail-icon.png');
            }
            &.tel{
               background-image: url('../images/common/footer-phone-icon.png');
               margin-right: 0;

               .line{
                  display:inline-block;
                  border-right: 1px solid #9c9c9c;
                  padding-right: 7px;
                  margin-right: 7px;
                  line-height: 1;
               }
            }
               
         }
      }

   
      a{
         font-size: 14px;
         &:hover{
            border-bottom:1px solid #000;
         }
      }

   }

   .adcdown{
      position: absolute;
      top:50px;
      right:5rem;
      padding-left: 15px;
      font-weight: 600;
      color:#000;
      background: url('../images/common/down-btn.png') 0 center no-repeat;
   }


   &:before{
      content:'';
      display:block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 200px;
      left: 0;
      background-color: #f5f5f5;
   }

   &.simple {
      background-color: #f5f5f5;

      .banner{
         display:none;
      }

      &:before{
         display:none;
      }
      &.dark{
         background-color: #0d0d0d;

         .place{
            background-image: url('../images/common/footer-location-icon-w.png');
         }
         .site{
            background-image: url('../images/common/footer-mail-icon-w.png');
         }
         .tel{
            background-image: url('../images/common/footer-phone-icon-w.png');
         }
         .adcdown{
            background-image: url('../images/common/down-btn-white.png');
         }
      }
   }
}

@media (max-width:1024px) {
   footer {
      padding: 0;

      .fbox{
         padding: 20px;
         
         ul {
            max-width: 100%;
            margin-bottom:15px;
   
            li {
               display: block;
               margin: 0 0 15px 0;
               &.place{
                  background-position: 0 5px;
               }
            }
         }
      }

      .adcdown{
         position: relative;
         top: inherit;
         right: inherit;
      }
   }
}
