@import 'style';

.MwaveWrap{
    .VisualTop{
        padding-bottom: 7.5rem;
        *{
            color: #fff;
            font-weight: 100;
        }
        background: #e70077 url('../images/project/mwave/line.png') 0 0 no-repeat;
        position: relative;
        li{
            float: left;
            padding-left: 4.16%;
            &:first-child{
                padding-left: 0;
            }
        }
        .clear{
            @include clear;
            position: relative;
        }
        .logo + .clear{
            padding-left: 14.375rem;
        }
        .logo{
            padding: 21.875rem 0 6.75rem;
            text-align: center;
        }
        .LeftTxt{
            float: left;
            width: 22%;
            padding-top: 6.875rem;
            h4{
                padding-top: 5.625rem;
                font-weight: 600;
                &:first-child{
                    padding-top: 0;
                }
            }
            ul, p{
                padding-top: 1.5rem;
            }
            li{
                &:last-child{
                    float: right;
                    padding-left: 0;
                }
            }
        }
        .RightImg{
            float: right;
            width: 78%;
            img{
                position: absolute;
                right: 2.2%;
                bottom: -8%;
                width: 17.39%;
                &:first-child{
                    position: static;
                    width: 95%;
                }
            }
        }
        .BottomTxt{
            padding-top: 4.375rem;
            padding-left: 14.375rem;
            *{
                font-size: 1.125rem;
            }
            li{
                p{
                    padding-top: .5rem;
                }
            }
        }
    }
    .UxPoint{       
        position: relative;
        width: 100%;
        max-width: 2560px;
        background: #e70077;
        *{
            color: #fff;
        }
        .txt{
            padding-top: 5.625rem;
            padding-left: 14.375rem;
            width: 59.27%;
            p{
                margin-top: 1.875rem;
            }
        }
        .ObjectWrap{
            min-height: 60vw;
            div[class^="object0"]{
                position: absolute;
            }
            .object01{
                width: 36.29%;
                left: 20.7%;
                top: 17.7%;
                z-index: 2;
            }
            .object02{
                width: 31.97%;
                left: 68.01%;
                top: 23.3%;
                z-index: 2;
                text-align: right;
            }
            .object03{
                width: 73.32%;
                left: 0;
                top: 28.64%;
                z-index: 1;
            }
            .object04{
                width: 63.84%;
                top: 12%;
                left: 30%;
            }
            .object05{
                width: 43.78%;
                right: -1%;
                top: 42.5%;
            }
            .object06{
                width: 37.35%;
                right: 0;
                top: -1.5%;
            }
            @media (min-width:1921px) {
                .object05{
                    right: -4%;
                }
            }
        }
    }
    .UiElement{
        position: relative;
        background: #f4f4f4;
        padding: 13.125rem 0 15.625rem 14.375rem;
        @media (min-width:1921px) {
            padding-top: 20rem;
        }
        .txt{
            width: 63.08%;
            p{
                margin-top: 1.875rem;
            }
        }
        .bigTxt{
            position: absolute;
            bottom: 10.625rem;
            left: 13.375rem;
            color: #b0b0b0;
            font-size: 13.75rem;
            opacity: 0.24;
            line-height: 0.9;
            letter-spacing: -16px;
        }
        .ObjectWrap{
            position: relative;
            padding-top: 9.375rem;
            @include clear;
            div[class^="object0"]{
                position: absolute;
                width: 24%;
            }
            .object01{
                top: 1%;
                left: 50%;
                margin-left: -17%;
                z-index: 2;
            }
            .object02{
                top: 17.8%;
                left: 50%;
                margin-left: -11%;
                z-index: 2;
            }
            .object03{
                top: 29.8%;
                left: 50%;
                margin-left: -23%;
                z-index: 2;
            }
            .object04{
                top: 47.5%;
                left: 50%;
                margin-left: -11.2%;
                z-index: 2;
            }
            .object05{
                top: 79.4%;
                left: 50%;
                margin-left: -17%;
                z-index: 2;
            }
            .fl{
                float: left;
                width: 45%;
                div{
                    padding-top: 6.25rem;
                    &:first-child{
                        padding-top: 0;
                    }
                }
                &:last-child{
                    margin-top: 8.125rem;
                }
            }
        }
    }
}

.MwaveMoWrap{
    *{
        color: #fff;
        font-size: 0.78rem;
    }
    h4{
        font-size: 0.956rem;
    }
    p{
        padding-top: .7rem;
    }
    .VisualTop{
        background: #e70077 url('../images/project/mwave/m-line.png') 0 -37% no-repeat;
        
        padding: 0 1.3rem 2.6rem;
        h4{
            padding: 2.275rem 0 0;
        }
        .imgBox{
            position: relative;
            padding: 4.55rem 0 0;
            margin: 0 -1.3rem;
            .logo{
                margin: 0 auto;
                width: 27.77%;
            }
            .object01{
                padding-top: 2.6rem;
                margin: 0 auto;
                width: 79.72%;
            }
            .object02{
                position: absolute;
                width: 22.22%;
                right: 8%;
                bottom: -5%;
            }
        }
        .txtBox{
            ul{
                padding-top: .7rem;
                li{
                    display: inline-block;
                    width: 50%;
                }
            }
        }
        > ul{
            padding-top: 2.6rem;
            li{
                padding-top: .7rem;
                &:first-child{
                    padding-top: 0;
                }
                span{
                    display: inline-block;
                    width: 70%;
                    &:first-child{
                        width: 30%;
                        vertical-align: top;
                    }
                }
            }
        }
    }
    .UxPoint{
        padding: 2.6rem 1.3rem 0;
        background: #e70077;
        .ObjectWrap{
            position: relative;
            min-height: 67vw;
            margin: 0 -1.3rem;
            div[class^="object0"]{
                position: absolute;
            }
            .object01{
                width: 40.83%;
                left: 11.7%;
                top: 24.7%;
                z-index: 2;
            }
            .object02{
                width: 21.94%;
                right: 0%;
                top: 40%;
                z-index: 2;
            }
            .object03{
                width: 70%;
                left: 0;
                top: 35.64%;
                z-index: 1;
            }
            .object04{
                width: 73.05%;
                top: 16.9%;
                left: 21.6%;
            }
            .object05{
                width: 46.38%;
                left: 53.5%;
                top: 54.5%;
                z-index: 1;
            }
            .object06{
                width: 41.38%;
                right: 0;
                top: 0;
            }
        }
    }
    .UiElement{
        position: relative;
        padding: 8.775rem 1.3rem 1.3rem;
        background: #f4f4f4;
        *{
            color: #222;
        }
        .ObjectWrap{
            position: relative;
            min-height: 583vw;
            margin: 5.85rem -1.3rem 0;
            div[class^="object"]{
                position: absolute;
            }
            .object01, .object05{
                width: 35.55%;
                right: 0;
            }
            .object01{
                top: -1%;
            }
            .object02{
                width: 41.66%;
                left: 12%;
                top: 14.5%;
            }
            .object03{
                width: 40.27%;
                right: 0;
                top: 32.5%;
            }
            .object04{
                width: 38.33%;
                left: 7.6%;
                top: 45%;
            }
            .object05{
                top: 64%
            }
            .object06, .object07 {
                width: 70.55%;
            }
            .object08, .object11, .object12{
                width: 65%;
            }
            .object01, .object02, .object03, .object04, .object05{
                z-index: 7;
            }
            .object06{
                z-index: 6;
                left: 1rem;
                top: 0;
            }
            .object07{
                z-index: 5;
                left: 21.6%;
                top: 18%;
            }
            .object08{
                z-index: 4;
                left: 1rem;
                top: 29.8%;
            }
            .object09{
                width: 59.44%;
                z-index: 3;
                left: 31%;
                top: 46.4%;
            }
            .object10{
                width: 72.22%;
                z-index: 3;
                left: 20%;
                top: 76%;
            }
            .object11{
                left: 1rem;
                top: 65.8%;
            }
            .object12{
                left: 1rem;
                top: 85%;
                z-index: 2;
            }
        }
        .bigTxt{
            position: absolute;
            bottom: .5rem;
            left: 1.3rem;
            font-size: 3.705rem;
            color: #b0b0b0;
            opacity: 0.24;
            line-height: 0.9;
            z-index: 1;
        }
    }
}