@import 'style';

.AsianaWrap{
    padding-top: 40px;
    .content{
        padding-top: 80px;
        margin-top: 0;
        background: #fff;
    }
    .asianaVisual{
        position: relative;
        width: 100%;
        max-width: 2560px;
        min-height: 40vw;
        .box{
            padding: 8% 0;
        }
        .logo{
            margin: 0 auto;
            width: 70.78%;
        }
        div[class^="object"]{
            position: absolute;
        }
        .object01{
            top: 0%;
            left: 0;
            width: 64.06%
        }
        .object02{
            top: -6.5%;
            left: 48.5%;
            width: 27.91%
        }
    }
    .txtBox {
        h3{
            font-size: 3.125rem;
            font-weight: 300;
        }
        p{
            font-size: 1.500rem;
            font-weight: 300;
        }
        span{
            position: relative;
            padding-left: 90px;
            &:before {
                content: '';
                display: inline-block;
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                margin: auto;
                background-color: #000;
                width: 70px;
                height: 1px;
            }
        }
    }
    div[class^="contArea"]{
        position: relative;
        margin-top: 150px;
        padding: 0 5.20%;
        width: 100%;
        > .inner{
            width: 100%;
            //min-height: 40vw;
        }
        &:first-child{
            margin-top: 0;
        }
    }
    .contArea1{
        *{
            color: #fff;
        }
        .inner{
            text-align: right;
            background: url('../images/project/asiana/asiana-con-1-bg.png') center 0 no-repeat;
            background-size: cover;
        }
        .txtBox{
            display: inline-block;
            width: 47%;
            text-align: right;
            padding-top: 11.875rem;
            padding-bottom: 10rem;
            padding-right: 6.25rem;
            p{
                padding-top: 10px;
            }
        }
        .object{
            position: absolute;
            top: 50%;
            left: 0;
            margin-top: -22%;
            width: 40.57%;
        }
        .icon{
            padding-top: 60px;
        }
    }
    .contArea2{
        *{
            color: #000;
        }
        .inner{
            text-align: left;
            background: url('../images/project/asiana/asiana-con-2-bg.png') center 0 no-repeat;
            background-size: cover;
        }
        .txtBox{
            display: inline-block;
            width: 44%;
            text-align: left;
            padding-top: 11.875rem;
            padding-bottom: 10rem;
            padding-left: 6.25rem;
            p{
                padding-top: 10px;
            }
        }
        .object{
            position: absolute;
            bottom: -10%;
            right: 0;
            width: 51.61%;
        }
        .icon{
            padding-top: 60px;
        }
    }
    .contArea3{
        *{
            color: #fff;
        }
        .inner{
            position: relative;
            text-align: right;
            background: url('../images/project/asiana/asiana-con-3-bg.png') center 0 no-repeat;
            background-size: cover;
            overflow: hidden;
        }
        .txtBox{
            display: inline-block;
            width: 48%;
            text-align: right;
            padding-top: 13.125rem;
            padding-bottom: 15rem;
            padding-right: 6.25rem;
            h3{
                display: inline-block;
                width: 64%;
            }
            p{
                padding-top: 10px;
            }
        }
        .object{
            position: absolute;
            top: 50%;
            left: -10rem;
            margin-top: -34%;
            //margin-top: -33.87rem;
            width: 59.84%;
        }
        .icon{
            padding-top: 60px;
        }
    }
    div.contArea4, div.contArea5, div.contArea6{
        padding: 0 8.3%;
    }
    .contArea4{
        .object{
            width: 41.66%;
            margin: 65px auto 0;
        }
    }
    .contArea5{
        .txtBox{
            padding-top: 11.875rem;
        }
        background: #f8f8f8;
        .objectWrap{
            position: relative;
            max-width: 2560px;
            min-height: 54vw;
            div[class^="object0"]{
                position: absolute;
            }
            .object01{
                right: 60%;
                top: 50%;
                margin-top: -25%;
                width: 28.39%;
            }
            .object02{
                right: 37.41%;
                bottom: 43%;
                width: 24.98%;
            }
            .object03{
                right: 15.5%;
                bottom: 64.46%;
                width: 24.69%;
            }
            .object04{
                right: -5.9%;
                bottom: 0;
                width: 24.62%;
            }
            .object05{
                right: 37.41%;
                bottom: 0;
                width: 24.62%;
            }
            .object06{
                right: 15.5%;
                bottom: 10.63%;
                width: 24.62%;
            }

        }
    }
    .contArea6{
        padding-bottom: 15% !important;
        .object01{
            margin: 50px auto 0;
            width: 75.52%;
        }
        .object02{
            position: absolute;
            right: 0;
            bottom: 0;
            width: 80.05%;
        }
    }
}

.AsianaMoWrap{
    .content{
        background: #fff;
    }
    .asianaVisual{
        position: relative;
        min-height: 15.12rem;
        display: table;
        .box{
            display: table-cell;
            vertical-align: middle;
        }
        .logo{
            padding-right: 1.43rem;
            padding-left: 2.75rem;
        }
        .object01{
            position: absolute;
            top: 20%;
            left: 0;
            width: 68.61%
        }
        .object02{
            position: absolute;
            top: 11%;
            left: 43.3%;
            width: 37.77%
        }
    }
    
    .txtBox {
        h3{
            font-size: 1.3rem;
            font-weight: 500;
        }
        p{
            padding: 1rem 0 2rem;
            font-size: 0.845rem;
            font-weight: 300;
        }
        span{
            display: block;
            padding-bottom: 5px;
            font-size: 0.78rem;
            color: #838383;
        }
    }

    div[class^="contArea"]{
        position: relative;
        width: 100%;
        > .inner{
            width: 100%;
            padding: 3.125rem 1.875rem 0;
        }
    } 
    .contArea1{
        *{
            color: #fff;
        }
        height: 42.75rem;
        overflow: hidden;
        background: url('../images/project/asiana/mo-asiana-con-1-bg.png') center 0 no-repeat;
        background-size: cover;
        .object{
            position: absolute;
            left: 3%;
            bottom: -13%;
            width: 94.16%;
        }
    }
    .contArea2{
        height: 42.75rem;
        overflow: hidden;
        background: url('../images/project/asiana/mo-asiana-con-2-bg.png') center 0 no-repeat;
        background-size: cover;
        .object{
            position: absolute;
            right: 0;
            bottom: 3%;
            width: 85.83%;
        }
    }
    .contArea3{
        *{
            color: #fff;
        }
        height: 42.75rem;
        overflow: hidden;
        background: url('../images/project/asiana/mo-asiana-con-3-bg.png') center 0 no-repeat;
        background-size: cover;
        .object{
            position: absolute;
            left: 0;
            bottom: 0;
        }
    }
    .contArea4{
        .object{
            margin: 4.6rem auto 4.25rem;
            //width: 83.61%;
        }
    }
    .contArea5{
        background: #f8f8f8;
        .object{
            margin: 2.5rem -30px 0;
        }
    }
    .contArea6{
        .object01{
            margin: 0 auto;
            padding: 2.06rem 0 8.87rem;
        }
        .object02{
            position: absolute;
            right: 0;
            bottom: 10%;
            width: 91.66%;
        }
    }
}