
@import 'style';

.main{
    margin-bottom:14.375rem;

    .video_wrap{
        // background-color: #ccc;
        margin-bottom: 13.75rem;

        img,
        video {
            width: 100%;
        }
    }
    .section_copy,
    .btn_wrap{
        // margin-bottom: 4.375rem;
        margin-bottom: 3.9rem;
    }

    .section_copy.work{
        margin-bottom:0.7rem;
    }

    .main_copy {
        margin-bottom: 9.375rem;

        .m_copy {
            margin-bottom: 20px;
            font-family: 'Roboto', sans-serif;
            font-size: 4.73rem;
            line-height: 1.2;
            font-weight: 700;
            list-style-position: -1px;
        }
        .s_copy {
            font-size: 3rem;
            line-height: 1.28;
            word-break: keep-all;
        }
    }

}




.main{
    @media (max-width:1280px) {
        .main_copy {
            margin-bottom: 6.875rem;
    
            .m_copy {
                font-size: 4.1rem;
            }
            .s_copy {
                font-size: 2.6rem;
            }
        }
        .video_wrap{
            margin-bottom: 6.875rem;
        }
    }
    
    @media (max-width:768px) {
        .s_copy{
            display: none;
        }
    }
    @media (max-width:480px) {
        margin-bottom:5rem;

        .video_wrap{
            margin-bottom: 3.75rem;

        }
        img,
        video{
            // width:auto;
            // height:420px;
        }
        .main_copy {
            margin-bottom: 70px;
            padding-left: 20px;

            .m_copy{
                font-size: 2.5rem;
            }
            .s_copy{
                font-size: 1.44rem;
            }
        }
        .abt{
            margin-top:1.5rem;
            margin-bottom: 5rem;
        }

    }

}
