
@import 'style';

header {
   position: relative;
   height: 140px;
   margin-bottom:100px;

   .hd_inner{
        position: relative;
        max-width:2560px;
        padding: 0 0 100px $leftArea;
        margin: 0 auto;   
   }

   .logo {
        position: absolute;
        display: inline-block;
        left: $leftArea;
        top: 60px;
        width: 171px;
        height: 21px;
        background: url('../images/common/common-adcapsule-ci@2x.png') 0 0 no-repeat;
        background-size: 100%;
        font-family: 'Roboto', sans-serif;
        font-weight: 900;

        h1{
            @include clear;
        }
        img{
            float:left;
            width:100%;
        }
   }

   .logo_dtp{
       position: absolute;
       display: inline-block;
       top: 46px;
       right: 77px;
       width: 159px;
       height: 54px;
       overflow: hidden;
       background: url('../images/common/common-no-1-slogan.png') 0 0 no-repeat;
       background-size: 100%;
   }

   .menu {
        position: fixed;
        top: 35px;
        left: 5rem;
        display: inline-block;
        width: 66px;
        height: 66px;
        padding: 0;
        z-index: 10;
        background-color: #fff;

        .line {
            @include posCenter;
            display: inline-block;
            position: absolute;
            width: 40%;
            height: 3px;
            background-color: #000;
            transition: transform $speed;

            &:before,
            &:after{
                content:'';
                display: block;
                position: absolute;
                width: 100%;
                height: 3px;
                background-color: #000;
            }

            &:before{
                top:-9px;
            }

            &:after{
                top:9px;
            }
        }
        &.active .line{
            transform: rotate(90deg);
        }

   }
}

nav {
    position: fixed;
    top: 167px;
    left: 8rem;
    visibility: hidden;
    z-index: 10;
    ul {
        transform-origin: 0 0;
        transform: rotate(90deg) translateX(0%);
        // transform: rotate(-90deg) translateX(-100%);
        @include clear;
        li {
            float:left;
            margin-right:3.3rem;
            
            a {
                font-family: 'Roboto', sans-serif;
                font-weight: 700;
                // font-size: 1.16rem;
                font-size: 17.5px;
                color:#000;
                
                &:hover,
                &.active {
                    text-decoration: underline;
                }
            }
            &:last-child{
                margin-right: 0;
            }
        }
    }
    .nav_close{
        display: none;
    }
}

@media (max-width:1024px) {

    header{
        height: 52px;
        margin-bottom: 70px;
        .menu {
            top: 10px;
            left: 15px;
            width: 38px;
            height: 38px;
            min-height: 38px;
            z-index: 10;
            background-color: #fff;

            .line {
                height: 2px;

                &:before,
                &:after{
                    height: 2px;
                }
                &:before{
                    top:-7px;
                }
    
                &:after{
                    top:7px;
                }
            }
        }

        .logo{
            top: 22px;
            left: 65px;
            width: 119px;
            height: 18px;
        }

        .logo_dtp{
            top: 20px;
            right: 20px;
            width: 25px;
            height: 25px;
            background-image: url('../images/common/common-no-1-slogan-m.png');
        }
    }

    nav{
        top: 80px;
        left: 45px;
    }

}

@media (max-width:480px) {

    nav {
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #000;
        
        transition: .2s ease-out; //draggable move

        .nav_close{
            position: absolute;
            top: 10px;
            left: 10px;
            display: inline-block;
            width: 50px;
            height: 50px;
            min-height: 50px;
            padding: 0;
            background-color: rgba(0,0,0,.4);
            &:before,
            &:after{
                content: '';
                display: inline-block;
                position: absolute;
                width: 50%;
                height: 2px;
                top: 50%;
                left: 0;
                right: 0;
                margin: auto;
                border-radius: 5px;
                background-color: #fff;
            }
            &:before{
                transform: rotate(45deg);
            }
            &:after{
                transform: rotate(-45deg);
            }
        }

        ul {
            padding: 144px 40px;
            transform: rotate(0deg) translateX(0px);
            li {
                display: block;
                float: inherit;
                margin:0 0 50px 0;
                a{
                    font-size: 1.8rem;
                    color: #fff;
                    font-weight: 400;
                }
            }
        }
    }

}