
@import 'style';

/****************************/
/* Dtp                      */
/****************************/

.dtp{
    padding-bottom: 13.75rem;

    img{
        max-width:100%;
    }

    section{
        position: relative;
        @include clear;
        &+section{
            margin-top: 7rem;
        }
    }

    .visual{
        position: absolute;
    }

    .sec_copy{
        word-break: keep-all;

        .tit_s{
            position: relative;
            font-size: 5.3rem;
            margin-top: 2.6rem;
            line-height: 1.2;
            font-weight: 500;

        }
        .desc{
            max-width:54rem;
            margin-top: 6.25rem;
            // font-size: 1.125rem;
            font-size: 18px;
            line-height: 1.83;
            font-weight: 300;
        }
    }

    .slogan_wrap{

        @include clear;

        > div{
            float:left;
        }
        .no1_mark{
            width:21%;
        }
        .no1{
            display: inline-block;
            width: 21rem;
            height: 21.5rem;
            max-width:100%;
            background: url('../images/dtp/dtp-slogan-img.png') 0 0 no-repeat;
            background-size: 100%;
        }

        .sec_copy{
            padding-left: 5rem;

            .slogan {
                display: inline-block;
                p{
                    font-family: 'Roboto';
                    font-weight: 500;
                    font-size: 7.4rem;
                    line-height: 1.1;
                }
            }

        }
    }

      
    .svgValue{
        display: inline-block;
        position: relative;
        font-size: 5vw;
        // border:1px solid red;
    }

    .svgmotion{
        position: absolute;
        top:-10%;
        left:-20%;
        width:150%;
        height:140%;
        border:1px solid green;
        transform: scale(1.3);
    
        mask path {
            fill: none;
            stroke: white;
            stroke-width: 60;
            stroke-dasharray:  1200;
            stroke-dashoffset: 1200;
            // animation: brush 3s ease-out infinite;
        }
    
        #pa path{
            fill:white;
        }
    }


    section{
        &.numberone{
            .visual{
                width:40%;
                top:24rem;
                right:0;
                text-align: right;
            }
            .sec_copy{
                width:79%;
                .desc{
                    width:66%;
                }
            }
        }

        &.ourvalue{

        }

        &.benefit{

            &.over{
                .bnf{
                    opacity: 0.2;
                    transition:.4s;
                    &.up{
                        opacity: 1;
                    }
                }
            }

            .sec_copy{
                padding-left: 0 !important;
                .tit{
                    position: relative;
                    z-index:10;
                }
            }
            .visual{
                width: 50%;
                position: absolute;
                top:28rem;
                right:0;
                img{
                    position:absolute;
                    top:0;
                    left:0;
                    visibility:hidden;
                    &.on{
                        visibility: visible;
                    }
                }
            }
            .bnfbox{
                max-width:1600px;
                margin-top: 8.68rem;
                @include clear;

                .bnf{
                    position: relative;
                    float:left;
                    border-top:1px solid #fff;
                    width:17.4rem;
                    min-height:21rem;
                    padding-top: 2.8rem;
                    
                    // bnf box layout control
                    margin: 0 1.9vw;

                    @media (min-width:1850px) {
                        margin: 0 3.5rem;
                    }


                    .tit{
                        font-weight: 600;
                        // font-size: 1.375rem;
                        font-size: 22px;
                        line-height: 1;
                    }
                    .desc{
                        padding-top: 1.125rem;
                        // font-size: 1.125rem;
                        font-size: 18px;
                        line-height: 1.7;
                        font-weight: 300;
                        word-break: keep-all;
                    }
                    &.book{
                        margin-left: calc(27.75rem - 4vw);
                        @media (min-width:1850px) {
                            margin-left: 27.75rem;
                        }
                    }
                    &.mindcare{
                        margin-right: 50rem;
                    }
                    &.amembers{
                        border-top: none;
                        width: 38%;
                        &:after{
                            content:'';
                            display: block;
                            position: absolute;
                            top: 0;
                            left: 0;
                            width:16.25rem;
                            height: 1px;
                            background-color: #fff;
                        }
                    }
                }
            }
        }

        &.dreamcapsule{
            .sec_copy{
                .desc{
                    max-width:100%;
                }
                .qa{
                    margin-top: 3.5rem;
                    font-size: 1.25rem;
                    color:#ffc059 !important;
                    a{
                        color:#ffc059 !important;
                        text-decoration: underline;
                    }
                }
            }

        }

    }

    @media (min-width:1651px) {
        .visual{
            position: absolute;
        }

        section{
            
            &+section{
                margin-top: 25rem;
            }

            &.ourvalue{
                padding-left: 30rem;

                &+section{
                    margin-top:16rem;
                }
                .visual{
                    top:0;
                    left:-10rem;
                }
                .years{
                    position: absolute;
                    top:0;
                    left:15rem;
                }
                .slogan_wrap{

                    .sec_copy{
                        width:100%;
                    }
                }
            }

            &.dreamcapsule{
                margin-top: 12rem;
                padding-left: 18rem;
                .visual{
                    top:-15rem;
                    left:-10rem;
                }
    
            }
        }
    }

    // 1650 미만 비주얼 absolute 해제
    @media (max-width:1650px) {
        padding: 0 20px 8rem;

        .visual{
            position: relative;
            top: inherit !important;
            right: inherit !important;
            left: inherit !important;
        }

        section{

            .sec_copy{
                .tit{
                    img{
                        width:100%;
                    }
                }
            }

            &.numberone{
                .visual{
                    width:100%;
                    margin-right: -20%;
                    float:right;
                }
                .sec_copy{
                    .desc{
                        width:100%;
                        max-width:100%;
                    }
                }
            }

            &.ourvalue{
                .visual{
                    width:50%;
                    margin-left: -6rem;
                    img{
                        margin-top: -10vw;
                    }
                }
                .years{
                    width:20%;
                }
                .sec_copy{
                    width:75%;
                    .tit{
                        width: 80%;
                    }
                }
            }

            &.benefit{
                .bnfbox{
                    margin-left: -2rem;
                    .bnf{
                        margin: 0 2rem !important;
                        width: calc(33% - 4rem);

                        &.amembers{
                            width: calc(100% - 4rem);
                            &:after{
                                width:calc(33% - 2rem);
                            }
                        }
                    }
                }
                .visual{
                    display: none;
                }
            }   

            &.dreamcapsule{
                margin-top: 14rem;
                .tit{
                    width:95%;
                    padding-left: 11vw;
                    padding-bottom: 2rem;
                }
                .visual{
                    position: absolute !important;
                    top:-19vw !important;
                    left:-11vw !important;
                    width:36%;
                }
            }

        }
    }

    @media (max-width:1024px) {
        section{
            &.benefit{
                .bnfbox{
                    .bnf{
                        margin: 0 2rem !important;
                        width: calc(50% - 4rem);

                        &.amembers{
                            width: calc(100% - 4rem);
                            &:after{
                                width:100%;
                            }
                        }
                    }
                }
                .visual{
                    display: none;
                }
            }   
        }
    }


    // 애매한 사이즈에서 font조정
    @media (min-width:481px) and (max-width:1650px) {

        section{
            .slogan_wrap{
                .sec_copy{
                    // border:1px solid green;
                    padding-left: 5vw;
                    .slogan p{
                        // border:1px solid red;
                        font-size: 6vw;
                    }
                    .tit_s{
                        // border:1px solid blue;
                        font-size: 4.5vw;
                    }
                    .desc{
                        margin-top: 3.5vw;
                    }
                }
            } 
        }
    }
    

    @media (max-width:768px) {
        section{

            &.benefit{
               
                .bnfbox{
                    .bnf{
                        float:inherit;
                        width:calc(100% - 3rem);
                    }

                }
            }

        }
    }

    @media (max-width:480px) {
    
        .slogan_wrap{
            .sec_copy{
                padding-left: 20px;
                
                .slogan {
                    
                    p{
                        font-size: 2rem;
                    }
                }
    
                .desc{
                    margin-top: 25px;
                    font-size: 0.845rem;
                }
            }

            .no1{
                width: 4.375rem;
                height: 4.375rem;
            }

        }
    

    
        section{

            &+section{
                margin-top: 5rem;
            }

            &.numberone{
                .visual{
                    width:90%;
                }
            }

            &.ourvalue{
                margin-top: 5.7rem;
                .visual{
                    margin-left: -10px;
                    img{
                        margin-top: -20vw;
                    }
                }
                .sec_copy{
                    .tit_s{
                        margin-top: 0.91rem;
                        font-size: 1.2rem;
                        line-height: 1.5;
                    }
                }
            }


            &.benefit{
                margin-top: 13rem;
                .sec_copy{
                    width:94%;
                    padding-left: 3rem !important;
                }
                .bnfbox{
                    margin-top: 7.3rem;
                    .bnf{
                        float:inherit;
                        border-top:1px solid #fff;
                        width:calc(100% - 3rem);
                        min-height:19rem;
                        margin: 0 0 0 3rem !important;
                        padding-top: 1.755rem;
                        @include clear;
                        .tit{
                            font-size: 1.1rem;
                        }
                        .desc{
                            max-width: 90%;
                            padding-top: 0.78rem;
                            font-size: 0.845rem;
                            line-height: 1.84;
                            font-weight: 300;
                            word-break: keep-all;
                        }
                        &.book{
                            margin-left: 26.75rem;
                        }
                        &.mindcare{
                            margin-right: 50rem;
                        }

                        &.birthday{
                            .desc{
                                max-width:60%;
                            }
                        }
                        &.healthcare{
                            .desc{
                                max-width:78%;
                            }
                        }
                        &.mindcare{
                            .desc{
                                max-width:80%;
                            }
                        }
                        &.longterm{
                            .desc{
                                max-width:100%;
                            }
                        }
                        &.amembers{
                            margin-bottom:5rem !important;
                        }

                        &:nth-child(odd){
                            text-align: right;
                            .desc{
                                float: right;
                            }
                        }
                        
                    }

                }
                .visual_welfare{
                    position: absolute;
                    right:-20px;
                    top:-5.3rem;
                }
                .visual_book{
                    position: absolute;
                    left:-2rem;
                    top:7rem;
                }
                .visual_flower{
                    position: absolute;
                    right:0;
                    top:6rem;
                }
                .visual_birthday{
                    position: absolute;
                    left:-2rem;
                    top:1.5rem;
                }
                .visual_healthcare{
                    position: absolute;
                    right:-1rem;
                    top:3.2rem;
                }
                .visual_mindcare{
                    position: absolute;
                    left:-2.5rem;
                    top:4rem;
                }
                .visual_longterm{
                    position: absolute;
                    right:0;
                    top:1.6rem;
                }                
                .visual_amembers{
                    position: absolute;
                    left:-2.5rem;
                    bottom:-3rem;
                }                
            }

            &.dreamcapsule{
                margin-top: 13rem;
                .sec_copy{
                    width:100%;
                    .qa{
                        margin-top: 1.6rem;
                        font-size: 0.78rem;
                    }
                }
                .tit{
                    padding-left: 5rem;
                    padding-bottom: 2rem;
                }
                .visual{
                    position: absolute !important;
                    top:-6.3rem !important;
                    left:-10px !important;
                }

            }

        }

    
    }

}
