
@import 'style';

.item_wrap{
    @include clear; 
    margin: 0 auto 5.625rem;
}

.item_card {
    position: relative;
    width: 770px;
    margin-bottom: 8.125rem;
    // float:left;
}

// reponsive
.item_card,
.grid-sizer { width: calc(46%); }
.grid-gutter {width: 8%}
// .item_card,
// .grid-sizer { width: calc(50% - 4.375rem); }
// .grid-gutter {width: 8.75rem}

// .item_card,
// .grid-sizer { width: 48%; }
// .grid-gutter {width: 4%}

#notice{
    .item_card,
    .grid-sizer { width: calc(30%); }
    .grid-gutter {width: 5%}
    @media (max-width:1280px){
        .item_card,
        .grid-sizer { width: calc(46%); }
        .grid-gutter {width: 8%}  
    }
}

.card{
    position: relative;
    display: block;
    color: #171a1c;

    .card_desc{
        margin-bottom: 0.81rem;
        .type{
            display: inline-block;
            font-family: 'Roboto', sans-serif;
            font-weight: 900;
            // font-size: 1rem;
            font-size: 16px;
            text-transform: capitalize;
        }

        .date{
            position: relative;
            display: inline-block;
            margin-left:12px;
            padding-left: 12px;
            font-family: 'Roboto', sans-serif;
            font-size: 16px;
            font-weight: 900;

            &:after{
                content:'';
                display:inline-block;
                position: absolute;
                top:4px;
                left:0;
                width:2px;
                height:10px;
                background-color: #c4c4c4;
            }

        }
    }
    .title{
        // font-size: 1.5rem;
        font-size: 24px;
        // font-weight: 300;
        line-height: 1.3;
        width:100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .img{
        width:100%;
        height: 57%;
        margin-bottom: 3.3rem;
        overflow: hidden;
        background-color: #eee;
        transition: $speed ease-out;

        img{
            width:100%;
            transform:scale(1);
            transition: $speed ease-out;

        }
    }
   
    .content{
        margin-top: 20px;
    }

    &:hover{
        .img{
            transform:scale(0.94);
        }
        img{
            transform:scale(1.15);
        }
    }
}

.filter{
    margin: 8.125rem 0 3.75rem;

    .filter_tg{
        display: none;
    }
    
    .filters-button-group{
        button {
            min-height: inherit;
            padding: 0;
            margin-right: 50px;
            font-size: 20px;
            font-family: 'Roboto', sans-serif;
            font-weight: 900;
            background: none;
            color: #000;
    
            &.on{
                text-decoration: underline;
            }
            &:last-child{
                margin: 0;
            }
        }
    }
}

.grid-item{
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 1px solid #ccc;
}


.loading_box{
    background-color: rgba(255,255,255,.9);
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    height: 100%;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;

    .text{
        display: inline-block;
        animation: loadMotion 1s ease-out infinite alternate;
    }

}

@keyframes loadMotion{
    0%{
        opacity: 0.7;
        transform: scale(1);
    }
    100%{
        opacity: 1;
        transform: scale(1.1);
    }
}

@media (max-width: 768px) {

    .filter{
        margin: 50px 0 20px;

        .filter_tg{
            display: block;
            position: relative;
            padding: 0;
            margin: 0 0 30px 0;
            min-height: inherit;
            // font-size: 0.91rem;
            font-size: 14.5px;
            font-weight: 600;
            color: #000;

            .arrow{
                position: absolute;
                display: inline-block;
                width: 12px;
                height: 12px;
                right: -20px;
                top: 10px;
                transform: rotateX(-180deg);
                transform-origin: 0 0;

                &:after,
                &:before{
                    content: '';
                    display: block;
                    width: 6px;
                    height: 1px;
                    background-color: #000;
                    position: absolute;
                    top: 0;
                }

                &:after{
                    transform: rotate(-45deg);
                    right: 1px;
                }

                &:before{
                    transform: rotate(45deg);
                    left: 1px;
                }
            }

            &:after{
                content: '';
                display: block;
                width: 100%;
                padding-right: 20px;
                height: 2px;
                background-color: #000;
                position: absolute;
                bottom: -6px;
                left: 0; 
            }

            &:hover{
                background: none;
            }
        }
        .filters-button-group{
            height: 175px;
            overflow: hidden;
            // transition: $speed;

            button {
                display: block;
                margin: 0 0 20px 0;
                // font-size: 0.91rem;
                font-size: 14.5px;
                font-weight: 600;
            }
        }

        &.fold{
            .filters-button-group{
                visibility: hidden;
                height: 0;
            }
            .arrow{
                transform: rotateX(0deg);
            }
        }
    }
    
    /* 5 columns for larger screens */
    .item_card,
    .grid-sizer { width: 100% !important; }
    .grid-gutter {width: 0 !important;}

    .item_card {
        // margin-bottom: 3.125rem;
    }

}

@media (max-width:480px) {
    .item_wrap{
        margin: 0 auto 3rem;
    }

    .card{
        .img{
            margin-bottom: 20px;
        }
        .card_desc{
            margin-bottom: 7px;
            .type,
            .date{
                font-size: 12px;
            }
            .date{
                &:after{
                    top:1px;
                }
            }
        }

        .title{
            font-size: 0.98rem;
        }
    }

    .item_card {
        margin-bottom: 50px;
    }

}

