
@import 'style';

.about_view{
    *{
        word-break: keep-all;
    }
    img{
        width: 100%;
    }
    .subTitle{
        margin-top: -15px;
        font-size:2.8125rem;
        font-weight: 400;
        line-height: 1.3;
        color: #000;
        b{
            display: block;
            font-size: 3.5rem;
            margin-bottom: 20px;
        }
    }
    .view_coltype{
        .tit{
            font-size: 1.125rem;
        }
        .desc{
            margin-top: 4rem;
            font-size:1.25rem;
        }
    }
    .visual{
        margin-left: -$leftArea;
    }

    .video_wrap{
        // background-color: #ccc;
        margin-bottom: 3.125rem;

        img,
        video {
            width: 100%;
        }
    }

    .servicesWrap{
        position: relative;
        &:before{
            content: '';
            position: absolute;
            top: 0;
            left: -14.375rem;
            z-index: -1;
            width: calc(100% + 14.375rem);
            height: 50rem;
            background: #f4f4f4;
        }
    }
    
    .itServices{        
        width: 100%;

        @include clear;
        > li{
            margin-top: 6.875rem;
            width: 25%;
            float: left;
            &:first-child{
                width: 20%;
            }
            &:nth-child(2){
                width: 30%;
            }
            h4{
                margin-bottom: 25px;
            }
            li{
                margin-top: 5px;
            }
        }
        
    }
    .teamInfo{
        width: 100%;

        @include clear;
        > li{
            margin-top: 6.875rem;
            padding-right: 6.25rem;
            width: 33.3%;
            min-height: 14rem;
            float: left;

            h4{
                padding: 1.56rem 0;
                border-top: 1px solid #c9c9c9;
            }
        }
    }
    .partnerList{
        @include clear;
        li{
            float: left;
            margin-top: 6.25rem;
            margin-right: 3.9375rem;
            width: 14.375rem;
        }
    }

    @media (max-width:1600px) {
        .itServices, .teamInfo{
            > li{
                width: 50% !important;
                font-size: 1.3rem;
                min-height:inherit;
            }
        }
    }

    @media (max-width:480px) {
        .itServices, .teamInfo{
            > li{
                width: 100% !important;
                float:none;
                padding:0;
                margin-top: 50px;
                font-size: 1rem;
            }
        }

        .view_coltype{

            .subTitle{
                font-size: 1.2rem;
                b{
                    font-size: 1.4rem;
                }
            }
            .desc{
                font-size: 1rem;
            }
        }
    }
}
