@import 'style';

.LotteDeptWrap{
    .VisualTop{
        *{
            color: #fff;
        }
        padding: 8.125rem 0 11.25rem 14.375rem;
        background: url(../images/project/lottedept/lottedept-top-img.jpg) 0 center no-repeat;
        background-size: 100% 100%;
        h2{
            margin-top: 3.75rem;
            font-size: 5.0625rem;
            font-weight: 400;
            line-height: 6.5rem;
        }
        ul{
            margin-top: 7rem;
            width: 40rem;
        }
        li{
            padding-top: 1.875rem;
            font-size: 1.25rem;
            &:first-child{
                padding-top: 0;
                p{
                    padding-top: 20px;
                }
                span{
                    font-size: 1.25rem;
                }
            }
            span{
                opacity: 0.7;
                font-size: 1.125rem;
            }
        }
        p{
            padding-top: 10px;
            font-size: 1rem;
        }
    }
    .txtWrap{
        position: relative;
        margin-left: -14.375rem;
        text-align: center;
        span{
            font-size: 2.25rem;
            color: #666;
        }
        h3{
            font-size: 4.5rem;
            font-weight: 400;
        }
        p{
            padding-top: 2.5rem;
            font-size: 1.5rem;
            color: #666;
        }
    }
    .App{
        position: relative;
        padding: 11.5625rem 0 0 0;
        &:before{
            content:'';
            position:absolute;
            top:0;
            left:0;
            width:100%;
            height:90%;
            background: linear-gradient(to right, #ffe3eb, #bbcaff) center 55.5rem no-repeat; 
            transform:skewY(-7deg);
            -webkit-transform:skewY(-7deg);
            -moz-transform:skewY(-7deg);
        }
        .top{
            position: relative;
            max-width: 1920px;
            margin: 0 auto;
            padding-left: 14.375rem;
            .txt{
                position: relative;
                width: 58.90%;
                z-index: 3;
            }
            .icon{
                margin-top: 6.875rem;
                width: 24.77%;
            }
            .phoneWrap{
                position: absolute;
                top: -12%;
                right: 2.3%;
                width: 53.1%;
                div{
                    position: absolute;
                    width: 100%;
                }
                .phone{
                    z-index: 2;
                }
                .phoneShadow{
                    z-index: 1;
                    top: 0;
                    left: 0;
                    width: 115%;
                }
            }
        }
        .Lpay{
            padding: 23.06rem 0 3rem 14.375rem;
            max-width: 1920px;
            margin: 0 auto;
            div{
                display: inline-block;
                margin: 0 -20px;
                width: 17.85%;
                &:nth-child(2){
                    width: 19.16%;
                }
                &:nth-child(3){
                    position: relative;
                    z-index: 1;
                    width: 24%;
                }
                &:nth-child(4){
                    margin-left: -64px;
                    width: 19.16%;
                }
            }
        }
        .txtWrap{
            margin-left: 0;
        }
        .point{
            max-width: 1920px;
            margin: 0 auto;
            padding-left: 14.375rem;
            @include clear;
            div{
                width: 20.99%;
                float: left;
                position: relative;
                margin-top: 8.75rem;
                margin-left: 10.625rem;
                text-align: center;
                &:first-child{
                    margin-left: 0;
                    margin-top: 13.75rem;
                }
                &:last-child{
                    margin-top: 5rem;
                }
                .phone{
                    position: relative;
                    margin: 0;
                    width: 100%;
                    height: 100%;
                    &:after{
                        content: '';
                        position: absolute;
                        top: 2%;
                        left: 4%;
                        z-index: 1;
                        background: url(../images/project/lottedept/lottedept-01-img-10.png) no-repeat;
                        background-size: cover;
                        width: 110%;
                        height: 105%;
                    }
                }
                img{
                    position: relative;
                    z-index: 2;
                }
                strong{
                    display: inline-block;
                    margin-top: 3.125rem;
                    font-size: 1.5rem;
                    font-weight:500;
                }
                p{
                    padding-top: 10px;
                    font-size: 1.125rem;
                    color: #444;
                }
            }
        }
    }
    .shopApp{
        position: relative;
        .txtWrap{
            position: absolute;
            top: 45%;
            left: 14.375rem;
            padding-left: 14.375rem;
            text-align: left;
            @media (min-width:1921px) {
                left: 50%;
                width: 1920px;
                max-width: 1920px;
                margin-left: -960px;
            }
            h3{
                font-size: 3.75rem;
            }
        }
    }
    .Web{
        .top{
            position: relative; 
            z-index: 2;
            max-width: 1920px;
            margin: 0 auto;
            padding: 16.875rem 0 0 14.375rem;
            .txt{
                width: 57.7%;
                position: relative;
                z-index: 2;
            }
            p{
                padding-top: 3.75rem;
                font-size: 1.5rem;
                color: #666;
            }
            .macBook{
                width: 51.66%;
                position: absolute;
                top: 25%;
                right: 0;
                z-index: 1;
            }
        }
        .top + div{
            margin-top: -2.6%;
        }
        .txtWrap{
            margin: 0;
            .img{
                width: 66.66%;
                padding-top: 5rem;
                margin: 0 auto;
            }
        }
        .DeptInfo{
            max-width: 1920px;
            margin: 0 auto;
            @include clear;
            padding: 0 0 0 3.75rem;
            > div{
                float: left;
                width: 47.31%;
                text-align: center;
                margin-left: 10px;
                margin-top: 7.5rem;
                &:first-child{
                    margin-left: 0;
                    margin-top: 13.75rem;
                }
                > div{
                    margin-top: 5rem;
                    &:first-child{
                        margin-top: 0;
                    }
                }
            }
            img{
                display: block;
                margin-bottom: 3.125rem;
            }
            strong{
                font-size: 1.5rem;
                font-weight:500;
            }
            p{
                padding-top: 10px;
                font-size: 1.125rem;
                color: #444;
            }
        }
        .DeptInfo + div{
            margin-bottom: 6rem;
        }
        .moblie{
            position: relative;
            max-width: 1920px;
            margin: 7.5rem auto 0;
            text-align: center;
            > div{
                display: inline-block;
                width: 31.97%;
                margin-left: 5.125rem;
            }
            .search{
                width: 36.875%;
                position: absolute;
                bottom: 7%;
                left: 13.6%;
                margin-left: 0;
            }
        }
    }
}

.LotteDeptMoWrap{
    .VisualTop{
        padding: 3.25rem 1.95rem 19.75rem;
        background: url(../images/project/lottedept/m-lottedept-top-img.png) center 0 no-repeat;
        background-size: cover;
        font-size: 0.78rem;
        color: #fff;
        li{
            width: 100%;
            padding-top: 0.65rem;
            span{
                display: inline-block;
                width: 17%;
                vertical-align: top;
                opacity: 0.7;
            }
            em{
                display: inline-block;
            }
            &:first-child{
                padding-top: 0;
            }
            &:last-child{
                padding-top: 1.3rem;
                span{
                    width: 100%;
                    padding-bottom: 0.65rem;
                }
                em{
                    line-height: 1.8;
                }
            }
        }
    }
    .txtWrap{
        text-align: center;
        span{
            font-size: 0.78rem;
            color: #666;
        }
        h3{
            font-size: 1.56rem;
            font-weight: 400;
        }
        p{
            padding-top: 1.95rem;
            font-size: 0.975rem;
            color: #666;
        }
    }
    .App{
        margin: 3.9rem 0 0;
        padding: 0 1.3rem;
        background: url(../images/project/lottedept/m-cont-app-bg-01.png) center 58% no-repeat;
        background-size: 100% 75%;
        .top{
            position: relative;
            .txt{
                position: relative;
                width: 84.4%;
                z-index: 2;
            }
            .icon{
                margin: 1rem 0 0 -4%;
                width: 35%;
            }
            .phoneWrap{
                position: absolute;
                top: -10%;
                right: -1.3rem;
                z-index: 1;
                width: 74.2%;
                .phone{
                    position: relative;
                    z-index: 2;
                }
                .phoneShadow{
                    position: absolute;
                    top: 0;
                    z-index: 1;
                    width: 115%;
                }
            }
        }
        .Lpay{
            margin: 8.4rem 0 2.11rem;
        }
        .point{
            @include clear;
            div{
                width: 29.5%;
                float: left;
                margin-top: 3.7375rem;
                margin-left: 1rem;
                text-align: left;
                &:first-child{
                    margin-left: 0;
                    margin-top: 4.875rem;
                }
                &:last-child{
                    margin-top: 2.6rem;
                }
                img{
                    display: block;
                    margin-bottom: .125rem;
                }
                strong{
                    font-size: 0.845rem;
                    font-weight: 500;
                }
                p{
                    padding-top: 5px;
                    font-size: 0.715rem;
                    color: #444;
                    letter-spacing: -.7px;
                }
            }
        }
    }
    .shopApp{
        margin-top: 1.795rem;
        padding: 5.72rem 0 4.81rem 1.3rem;
        background: url(../images/project/lottedept/m-lottedept-01-img-05.png) center 0 no-repeat;
        background-size: cover;
        .txtWrap{
            text-align: left;
            h3{
                font-size: 1.17rem;
            }
            p{
                font-size: 0.78rem;
            }
        }
    }
    .Web{
        margin-top: 1.95rem;
        .top{
            position: relative;
            padding: 11.7rem 0 0 1.3rem;
            .txt{
                width: 84.4%;
            }
            p{
                padding-top: 1.625rem;
                font-size: 0.78rem;
                color: #666;
            }
            .macBook{
                width: 68.61%;
                position: absolute;
                top: 4%;
                right: 0;
                z-index: 1;
            }
        }
        .txtWrap{
            margin-top: 1.95rem;
            padding: 0 1.3rem;
            p{
                padding: 1.625rem 0 2.6rem;
                font-size: 0.78rem;
            }
        }
        .grBack{
            background: #f6f6f6;
        }
        .DeptInfo{
            padding: .5rem 1.3rem 3.9rem;
            text-align: center;
            > div{
                margin-top: 1.95rem;
                strong{
                    font-size: 0.975rem;
                }
                p{
                    font-size: 0.78rem
                }
            }
        }
        .moblie{
            position: relative;
            text-align: center;
            > img{
                width: 81.11%;
                margin-left: 3rem;
            }
            .search{
                position: absolute;
                top: 28%;
                left: 6%;
                width: 93.88%
            }
        }
    }
}