
@import 'style';

/****************************/
/* contact                  */
/****************************/

$cntMar : 5rem;
$cntSize : 1.375rem;
$cntSizeFocus : 1.875rem;
$m_cntSize : 0.875rem;
$m_cntSizeFocus : 1rem;


#contact{
    position: relative;
    width:100%;

    border:1px solid transparent;
    min-height:calc(100vh - 380px);

    .section_copy{
        margin-top: 0;
    }

    .contact_wrap{
        position: relative;
        background-color: #f5f5f5;
        margin: 50px 0;
        padding-bottom: 100px;
        .action{
            margin-left: $cntMar;
        }
    }

    .contact {
        padding: $cntMar;
        padding-bottom: 0;
    }
    .ipt_wrap {
        position: relative;
        margin-bottom: 3rem;
        visibility: hidden;
        opacity: 0;
        transform: translateY(10px);
        transition: .3s;
        font-size: $cntSize;
        font-weight: bold;

        @include clear;

        .select_custom{
            float:left;
            font-size: $cntSize;
            *{
                color:#888;
            }

            ul li a{
                &:hover span{
                    color:#000;
                }
                span{
                    color:#888;
                }
            }
            .arrow{
                &:after,
                &:before{
                    background-color: #888;
                }
            }
        }

        &.focus{
            .select_custom{
                width:220px;
                @media (max-width:480px){
                    width:128px;
                }
                .arrow{
                    &:after,
                    &:before{
                        background-color: #000;
                    }
                }
            }
        }



        input[type=text]{
            height:auto;
            min-width:60px;
            margin: 0 10px;
            padding: 0;
            border: 0;
            vertical-align: bottom;
            text-indent: 0;
            color:#888;
            font-size: $cntSize;
            font-weight: bold;
            border-bottom: 1px solid #000;
            outline:none;
            transition: 0s;
        }

        textarea {
            padding: 0;
            text-indent: 0;
            font-size: $cntSize;
            color:#888;
            border:0;
            border-bottom:1px solid #888;
            font-weight: bold;
            outline:none;
            // transition: $speed;
        }

        .leng_check{
            position: absolute;
            bottom: 10px;
            right: 10px;
            display: inline-block;
            width: inherit !important;
            font-size: 1rem !important;
            color:#888 !important;
            *{
                font-size: 1rem !important;
                color:#888 !important;
            }
        }

        .checkwrap {
            margin-top:100px;
            label{
                line-height: 1;
                span {
                    font-size: $cntSize;
                    color:#000;
                    position: relative;
                    &:after{
                        content:'';
                        display:block;
                        position: absolute;
                        bottom: 3px;
                        left:0;
                        width:100%;
                        height:1px;
                        background-color: #000;
                    }
                }
            }
        }

        &.default,
        &.show{
            visibility: visible;
            opacity: 1;
            transform: translateY(0);
        }
        &.entered{
            font-weight: bold;
            color:#888;

            input[type=text]{
                border-bottom: 1px solid #888;
            }

        }
        &.focus{
            color:#000;
            font-size: $cntSizeFocus;
            * {
                font-weight: bold;
                color:#000;
                font-size: $cntSizeFocus;
            }

            input[type=text],
            textarea {
                color:#000;
                font-size: $cntSizeFocus;
            }
        }

        &.col2{
            > span {
                float:left;
                min-width: 130px;
            }
            > div,
            textarea{
                float:left;
                width: calc(100% - 130px);
            }
        }
            
        .file {
            .btnwrap{
                @include clear;
                margin-top: 5px;
            }
            label{
                display:table;
                float:left;
                span{
                    display:table-cell;
                    vertical-align: middle;
                }
            }
            .txt{
                float:left;
                margin-left: 10px;
                font-size: 1.25rem;
                color:#3c3c3c;
            }

            #uploadList{
                margin-top: 30px;
                max-width: 375px;
                .list {
                    border-bottom:1px solid #bbb;
                    margin-bottom:20px;
                    font-size: $cntSize;
                    color:#000;
                    @include clear;

                    &:last-child{
                        margin-bottom: 0;
                    }
            
                    > span {
                        display:inline-block;
                        max-width: calc(100% - 30px);
                        text-overflow: ellipsis;
                        overflow: hidden;
                        white-space: nowrap;
                    }
            
                    .close{
                        position: relative;
                        float:right;
                        display: inline-block;
                        width: 30px;
                        height: 30px;
                        min-height: 30px;
                        padding: 0;
                        &:before,
                        &:after{
                            content: '';
                            display: inline-block;
                            position: absolute;
                            width: 50%;
                            height: 1px;
                            top: 50%;
                            left: 0;
                            right: 0;
                            margin: auto;
                            background-color: #000;
                        }
                        &:before{
                            transform: rotate(45deg);
                        }
                        &:after{
                            transform: rotate(-45deg);
                        }
                    }
                    
                }
            }
        }

    }

    .fake{
        visibility:hidden;
        min-width:60px;
        position:absolute;
        right:0;
        border: 1px solid red;
        display: inline-block;
        font-weight: bold;
        color:#aaa;
        font-size: $cntSize;

        &.focus{
            color:#000;
            font-size: $cntSizeFocus;   
        }
    }

    .btn_next{
        transition:.2s;
    }

    .cnt_link{
        margin: 120px 0 200px;

        .lkwrap{
            &:first-child{
                margin-bottom: 50px;
            }
            a {
                position: relative;
                font-size: $cntSizeFocus;
                font-weight: 600;

                &.on{
                    text-decoration: underline;
                }
            }
        }
    }

    .cnt_head{
        display: none;
    }



    @media (max-width:480px) {
        .contact *{
            // font-size:100% !important;
        }

        .contact{
            height: calc(100% - 50px);
            overflow-y: auto;
        }



        .ipt_wrap {
            font-size: $m_cntSize;
            input[type=text]{
                font-size: $m_cntSize;
            }
            textarea {
                font-size: $m_cntSize;
            }
            .checkwrap {
                label{
                    span {
                        font-size: $m_cntSize;
                    }
                }
            }

            .select_custom{
                font-size: $m_cntSize;
            }
        
            &.focus{
                font-size: $m_cntSizeFocus;
                * {
                    font-size: $m_cntSizeFocus;
                }
        
                input[type=text],
                textarea {
                    font-size: $m_cntSizeFocus;
                }
            }
        
            .file {
                .txt{
                    font-size: 0.8rem;
                }
        
                #uploadList{
                    .list {
                        font-size: $m_cntSize;
                        .close{
                            width:25px;
                            height:25px;
                            min-height:25px;
                        }
                    }
                }
            }
        }
        
        .fake{
            font-size: $m_cntSize;
            &.focus{
                font-size: $m_cntSizeFocus;   
            }
        }

        .formwrap.file label{
            font-size: .8rem;
        }
        
        .cnt_link{
            .lkwrap{
                a {
                    font-size: $m_cntSizeFocus;
                }
            }
        }    


        .cnt_project{
            position: fixed;
            top:0;
            left:0;
            width: 100%;
            height:100%;
            z-index: 99;

            .contact_wrap{
                height: 100%;
                margin-top: 42px;
                padding-bottom: 30px;
                .contact{
                    padding:20px;
                }
                .action{
                    position: fixed;
                    z-index:10;
                    bottom: 0;
                    width: 100%;
                    height:60px;
                    font-size: 1rem;
                    margin-left: 0;
                    text-align: center;
                    &:after{
                        display: none;
                    }
                }
            }
            
        }

        .cnt_head{
            display: block;
            position: fixed;
            top:0;
            left:0;
            width: 100%;
            height: 42px;
            z-index: 99;
            background-color: #fff;
            @include clear;

            .prev{
                position: relative;
                float: left;
                width:40px;
                height:40px;
                margin-left: 5px;
                &:after,
                &:before{
                    content:'';
                    display: inline-block;
                    position: absolute;
                    width:10px;
                    height: 1px;
                    background-color: #000;
                }
                &:after{
                    transform:rotate(45deg);
                    left:15px;
                    top:24px;
                }
                &:before{
                    transform:rotate(-45deg);
                    left:15px;
                    top:17px;
                }

            }
            .loc_tit{
                float:left;
                margin-left: 10px;
                line-height: 42px;
                font-size: 0.875rem;
                font-weight: 600;
                color:#000;
            }
        }

        .ipt_wrap{

            &.col2{
                > span {
                    float:inherit;
                    min-width: 100%;
                }
                > div,
                textarea{
                    float:inherit;
                    width: 100%;
                }
            }

            .file {
                .btnwrap{
                    margin-top: 10px;
                }
                label{
                    float: inherit;
                    width:100%;
                    height:40px;
                    margin-bottom: 10px;
                }
                .txt{
                    float:inherit;
                    margin-left: 0;
                }
            }

            .checkwrap {
                margin-top:50px;
            }
        }


        .cnt_link{
            margin: 80px 0 100px;
        
            .lkwrap{
                padding-left: 68px;
                
                &:first-child{
                    margin-bottom: 30px;
                }
                
                a {
                    padding-left: 15px;
                    font-size: 1rem;
                    font-weight: 600;
                    &:after{
                        content: '';
                        position: absolute;
                        display: inline-block;
                        top:12px;
                        left:0;
                        width:8px;
                        height:2px;
                        background-color: #000;
                    }
                }
            }
        }
        

    }


}
