
@import 'style';

.item_view{
    position: relative;
    margin-top: 50px;

    // position: fixed;
    // width: 100% !important;
    // height: 100%;
    // background: #fff;
    // z-index: 999;
    // top:0;
    // left:0;
    // overflow-y: auto;

    .inner{
        max-width: 2560px;
        margin: 0 auto;
        // background-color: #fff;
    }

    .visual{
        // margin-bottom: 20px;
        img{
            width:100%;
        }
    }
    .desc{
        margin-bottom: 9.375rem;
        word-break: keep-all;
    }
    .close{
        display: inline-block;
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        top: 20px;
        right: 20px;
        background-color: rgba(0,0,0,.4);
        &:before,
        &:after{
            content: '';
            display: inline-block;
            position: absolute;
            width: 50%;
            height: 2px;
            top: 50%;
            left: 0;
            right: 0;
            margin: auto;
            border-radius: 5px;
            background-color: #fff;
        }
        &:before{
            transform: rotate(45deg);
        }
        &:after{
            transform: rotate(-45deg);
        }
    }
}

.prinfo{
    position: relative;
    margin: 5rem 0;
    @include clear;
    li{
        width: 25%;
        float: left;
        font-size: 1.17rem;
        
        .cate{
            margin-bottom: 10px;
            color: #8e8e8e;
        }
        .prlink{
            padding-right: 42px;
            background: url('../images/common/ourwork-view-arrow-icon.png') right center no-repeat;
        }
    }
}

.view_coltype {
    padding: 9.375rem 6.25rem 3.75rem 0;
    max-width:2560px;
    // text-align: justify;

    @include clear;
    > div{
        float:left;
    }
    .left{
        width:16.25rem;
    }
    .right{
        width: calc(100% - 16.25rem);
    }

    .tit {
        position: relative;
        margin-bottom: 50px;
        padding-left: 90px;
        font-weight: 900;
        font-size: 1.4rem;

        &:before{
            content: '';
            display: inline-block;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            background-color: #000;
            width: 70px;
            height: 2px;
        }
    }
    .desc {
        font-size: 1.5rem;
        line-height: 1.8;
    }

    &.coltype{
        > p{
            float: left;
        }
        .cate{
            display:inline-block;
            width: 260px;
        }
        .desc{
            width: calc(100% - 260px);
            font-size: 24px;
            line-height: 1.8;
        }
    }
}

.loading_box{
    background-color: rgba(255,255,255,.9);
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    height: 100%;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;

    .text{
        display: inline-block;
        animation: loadMotion 1s ease-out infinite alternate;
    }

}


.type_view{
    .inner_left,
    .btn_wrap{
        max-width: 2560px;
        padding: 0 0 0 $leftArea;
        margin-left: auto;   
        margin-right: auto;   
    }

    .content{
        padding-top:18.75rem;
        margin-top:-18.75rem;
        padding-bottom: 10rem;
        background-color: #f5f5f5;
    }

    img{
        max-width: 100%;
    }
}

// Project Template
.template{
    .visual{
        img{
            width: 100%;
        }
        @media (max-width:480px){
            margin-left:-48px;
        }
    }
    .thumb_set{
        margin:-10rem 0 9rem;
    }
    .descbox{
        margin-left: 16.25rem;
        padding: 0 6.25rem 9rem 14.375rem;
        width: calc(100% - 16.25rem);
        color:#000;
        b{
            font-size: 1.375rem;
        }
        p{
            padding-top: 2rem;
            font-size: 1.25rem;
            line-height: 1.8;
            &:first-child{
                padding-top: 0
            }
        }
        b + p{
            padding-top: 0;
        }
    }
    .imgbox{
        text-align:center;
        font-size: 2rem;
    }
}

@keyframes loadMotion{
    0%{
        opacity: 0.7;
        transform: scale(1);
    }
    100%{
        opacity: 1;
        transform: scale(1.1);
    }
}

@media (max-width: 1600px) {
    .prinfo{
        margin: 40px 0;
        li{
            width: 50%;
            margin-bottom: 20px;
        }
    }

}
@media (max-width: 768px) {

    .view_coltype {
        padding: 50px 20px 20px 0;
        
        > div,
        .left,
        .right{
            float: inherit;
            width: 100%;
        }
    }

    .type_view {

        .content{
            padding-top:6.25rem;
            margin-top:-6.25rem;
            padding-bottom: 0;
        }
    }

    .template{
        .thumb_set{
            margin-top:0;
        }
    }
}

@media (max-width:480px) {
    .prinfo{
        li{
            width: 100%;
            float: inherit;
            font-size: .78rem;
        }
    }

    .item_view.inner_left{
        padding-left: 0px !important;
        .desc{
            margin-bottom: 20px;
        }
      
    }

    .type_view{
        .btn_wrap{
            max-width:100%;
            padding: 0;
            margin:0;
        } 
    }

    .view_coltype {

        .tit {
            margin-bottom: 20px;
            padding-left: 35px;
            font-size: .85rem;
            &:before{
                width: 25px;
            }
        }
        .desc {
            font-size: 1.1rem;
            line-height:1.5;
        }
    }


}

