@import 'style';

.holisticWrap{
    .visualTop{
        padding-top: 13.75rem;
        background: url(../images/project/holistic/pattern-bg.png) 0 center no-repeat;
        background-size: cover;
        .logo{
            width: 8.38%;
            margin: 0 auto;
        }
        h2{
            padding: 2.5rem 0 5.625rem;
            font-size: 4.75rem;
            font-weight: 400;
            line-height: 0.97;
            text-align: center;
            color: #f4eee4;
        }
        .ipad{
            position: relative;
            .bg{
                position: absolute;
                width: 45.54%;
                z-index: 2;
                top: -10%;
                left: 50%;
                margin-left: -22.5%;
            }
        }
        .info{
            @include clear;
            margin: 6.25rem 14.375rem 0;
            padding: 1.875rem 0 7.5rem;
            border-top: 1px solid #d0c1a7; 
            *{
                color: #fff;
                font-size: 1.125rem;
            }
            > div{
                > div{
                    margin-top: 2.5rem;
                    &:first-child{
                        margin-top: 0;
                    }                    
                }
                p{
                    margin-top: 10px;
                }
                float: left;
                width: 25%;
                &:last-child{
                    width: 50%;
                }
            }
        }
    }
    h3{
        padding-top: 5.625rem;
        font-size: 3rem;
        font-weight: 400;
        color: #5d5d5d;
        text-decoration: underline;
    }
    ol{
        li{
            position: relative;
            padding: 0 14.375rem;
            p{
                width: 56.13%;
                padding-top: 1.8rem;
                font-size: 1.125rem;
            }
            div[class^="img"]{
                position: absolute;
            }
            &:first-child{
                min-height: 72vw;
                background: #9f896e;
                h3{
                    color: #ede7dc;
                }
                p{
                    color: #fff;
                }
                .img1{
                    width: 41.19%;
                    right: 52.23%;
                    bottom: 34%;
                }
                .img2{
                    width: 41.19%;
                    right: 19.84%;
                    bottom: 47%;
                }
                .img3{
                    width: 41.19%;
                    right: 2%;
                    bottom: 34%;
                }
                .img4{
                    width: 41.19%;
                    right: 52.65%;
                    bottom: 8%;
                }
                .img5{
                    width: 41.66%;
                    right: 26.77%;
                    bottom: 21%;
                }
                .img6{
                    width: 41.19%;
                    right: 34.16%;
                    bottom: -5%;
                    z-index: 2;
                }
                .img7{
                    width: 41.19%;
                    right: 2%;
                    bottom: 8%;
                }
                .img8{
                    width: 17.76%;
                    right: 0;
                    bottom: 26%;
                }
            }
            &:nth-child(2){
                padding-bottom: 11.875rem;
                background: url(../images/project/holistic/bg-gray.png) 0 center no-repeat;
                background-size: cover;
                p{
                    padding-bottom: 5.625rem;
                }
                .img{
                    top: 8.2%;
                    right: 7%;
                    width: 20.10%;
                }
                @media (min-width:1921px) {
                    .img{
                        top: 3.2%;
                        right: 2%;
                    }
                }
            }
            &:nth-child(3){
                background: #fff;
                h3{
                    position: absolute;
                    top: -7%;
                    padding: 0;
                }
                p{
                    padding-top: 4.375rem;
                }
                .txtImg{
                    margin: 4rem 0 7.5rem;
                }
                .video_wrap{
                    margin: 0 -14.375rem;
                }
            }
            &:last-child{
                min-height: 90vw;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 80%;
                    left: 0;
                    background-color: #f4f4f4;
                }
                .img1{
                    width: 100%;
                    top: 35%;
                    left: 0;
                }
                .img2{
                    width: 11.09%;
                    right: 40%;
                    top: 24%;
                }
                .img3{
                    width: 16.82%;
                    bottom: 9%;
                    right: 0;
                }
                .img4{
                    width: 77.55%;
                    bottom: 0;
                    left: 0;
                }
                .img5{
                    width: 56.56%;
                    top: 17%;
                    right: 0;
                }
            }
        }
    }
}

.holisticWrap + .btn_wrap{
    background: #f4f4f4;
    padding: 3.75rem 0 8rem 14.375rem;
    margin: auto;
}

.holisticMoWrap{
    .visualTop{
        padding: 3.25rem 0;
        background: url(../images/project/holistic/m-pattern-bg.png) 0 center no-repeat;
        background-size: cover;
        .logo{
            width: 13.33%;
            margin: 0 auto;
        }
        h2{
            padding: 1.3rem 0 3.9rem;
            font-size: 1.95rem;
            font-weight: 400;
            line-height: 0.93;
            text-align: center;
            color: #f4eee4;
        }
        .ipad{
            position: relative;
            .bg{
                position: absolute;
                width: 45.54%;
                z-index: 2;
                top: -10%;
                left: 50%;
                margin-left: -22.5%;
            }
        }
    }
    .info{
        *{
            color: #fff;
            font-size: .78rem;
        }
        position: relative;
        background: #866e4e;
        padding: 1.95rem 2.6rem 13.52rem;
        span, em{
            display: inline-block;
        }
        span{
            width: 25%;
        }
        li{
            padding-top: 5px;
            &:last-child{
                border-top: 1px solid #d0c1a7;
                margin-top: 0.975rem;
                padding-top: 1.625rem;
                span{
                    width: 100%;
                    padding-bottom: 10px;
                }
            }
        }
        .img{
            position: absolute;
            bottom: -25%;
            left: 0;
            z-index: 2;
        }
    }
    h3{
        font-size: 1.17rem;
        color: #5d5d5d;
        text-decoration: underline;
    }
    ol{
        li{
            position: relative;
            padding: 0 2.6rem;
            p{
                padding-top: 0.845rem;
                font-size: 0.845rem;
                color: #000;
            }
            div[class^="img"]{
                position: absolute;
            }
            &:first-child{
                margin-top: 9rem;
                margin-bottom: 8.84rem;
            }
            &:nth-child(2){
                background: url(../images/project/holistic/m-bg-gray.png) 0 center no-repeat;
                background-size: cover;
                padding: 19.175rem 2.6rem 3.25rem;
                .img1{
                    top: -15%;
                    width: 81.66%;
                }
                .img2{
                    top: -25%;
                    right: 0;
                    width: 28.05%;
                }
            }
            &:nth-child(3){
                h3{
                    margin-top: 1.3rem;
                }
                .video_wrap{
                    margin: 3.25rem -2.6rem 0;
                }
            }
            &:last-child{
                padding: 3.25rem 2.6rem 37.05rem;
                background: #f4f4f4;
                .img1{
                    width: 100%;
                    left: 0;
                    top: 50%;
                }
                .img2{
                    width: 18.05%;
                    right: 14%;
                    top: 38%;
                }
                .img3{
                    width: 24.72%;
                    left: 0;
                    top: 30%;
                }
                .img4{
                    width: 100%;
                    top: 35%;
                    left: 0;
                }
            }
        }
    }
}