@import 'style';

.beautyPoint{
    h2{
        font-size: 7.5rem;
        font-weight: 400;
        span{
            font-weight: 100;
        }
        p{
            font-size: 1.875rem;
        }
    }
    text-align:center;
    h3{
        font-size: 5.4rem;
        font-weight: 400;
        color: #333;
        p{
            margin-top: 30px;
            font-size: 1.375rem;
        }
    }
    .visual{
        padding: 130px 0 120px;
        color: #fff;
        background: url('../images/project/beautyPoint/beautypoint-ci.png') center no-repeat, linear-gradient(to right, #99d7ff, #fda4bd 49%, #fcd49f);
        .img{
            margin: 120px 0 0 200px;
        }
        .prInfo {
            width: 100%;
            *{
                font-size: 1.063rem;
                text-align: left;
                color: #fff;
            }
            //@include clear;
            > div{
                display: inline-block;
                width: 20%;
                &:first-child{
                    margin-left: 0;
                }
                &:last-child{
                    width: 30%;
                }
                p{
                    &:nth-child(odd){
                        padding-top: 65px;
                    }
                    &:nth-child(even){
                        padding-top: 10px;
                    }
                    &:first-child{
                        padding-top: 0;
                    }
                }
            }
        }
    }
    .grFont{
        margin-top: 200px;
    }
    .inner{
        padding:0 11%;
    }
    .cont1{
        margin-top: 250px;
        background: url('../images/project/beautyPoint/beautypoint-con-01-shape.png') 0 540px no-repeat;
        background-size: contain;
        .moArea{
            @include clear;
            margin: 100px 0 0 0;
            div{
                float: left;
                width:calc(33% - 95px);
                margin-left: 95px;
                border-radius: 55px;
                box-shadow: 100px 50px 130px rgba(0, 0, 0, 0.15);
                &:first-child {
                    margin: 0;
                }
                &:nth-child(2) {
                    margin-top: 190px;
                }
                &:nth-child(3) {
                    margin-top: 375px;
                }
            }
        }
    }
    .cont2{
        position: relative;
        margin-top: 365px;
        padding-bottom: 365px;
        background: url('../images/project/beautyPoint/beautypoint-con-02-shape-01.png') 0 bottom no-repeat;
        .inner{
            background: url('../images/project/beautyPoint/beautypoint-con-02-shape-03.png') right 100px no-repeat;
        }
        ul{
            text-align: left;
            li{
                position: relative;
                margin-top: 135px;
                @include clear;
                height: 818px;
                .moArea{
                    float: right;
                    margin-right: -$leftArea;
                    @media (min-width:1921px){
                        margin-right: -18.375rem;
                    }
                    > div{
                        margin-left: 80px;
                        border-radius: 55px;
                        float: left;
                        box-shadow: 100px 50px 130px rgba(0, 0, 0, 0.15);
                    }
                }
            }
        }
        h4{
            position: absolute;
            top: 50%;
            width: 26%;
            margin-top: -87px;
            float: left;
            font-size: 2.8rem;
            p{
                margin-top: 50px;
                font-size: 1.125rem;
            }
        }
        .shape{
            position: absolute;
            bottom: 0;
            left: 345px;
        }
    }
    .cont3{
        position: relative;
        margin-top: 160px;
        padding-bottom: 250px;
        background: url('../images/project/beautyPoint/beautypoint-con-03-shape-04.png') 0 bottom no-repeat;
        background-size: contain;
        .shape1 {
            position: absolute;
            top: 1100px;
            left: 35px;
        }
        .shape2{
            position: absolute;
            left: 388px;
            bottom: 240px;
        }
        .smartService{
            margin-top: 50px;
            background: url('../images/project/beautyPoint/beautypoint-con-03-bg.png') 0 -300px no-repeat;
            background-size: cover;
            .inner{
                max-width: 1920px;
                margin: 0 auto;
                padding: 0 11%;
            }
            li{
                position: relative;
                @include clear;
                text-align: left;
                color: #000;
                h4{
                    float: left;
                    font-size: 3rem;
                    p{
                        margin-top: 50px;
                        font-size: 1.125rem;
                    }
                }
                &:first-child {
                    padding-top: 270px;
                    color: #fff;
                    h4{
                        padding: 100px 0 0 150px;
                    }
                }
                &:last-child {
                    margin-right: -$leftArea;
                    h4{
                        padding: 50px 0 0 260px;
                    }
                    .moArea2{
                        float: right;
                        margin-top: -430px;
                    }
                }
                .moArea{
                    float: left;
                    > div{
                        position: relative;
                        top: 0;
                        left: 0;
                        &:first-child {
                            position: absolute;
                            top: 70px;
                            left: 70px;
                            z-index: 2;
                        }
                    }
                }
                .rotating {
                    position: absolute;
                    top: -140px;
                    left: 30px;
                    z-index: -1;
                    width: 664px;
                    height: 662px;
                    background: url('../images/project/beautyPoint/beautypoint-con-03-shape-01.png') no-repeat;
                    -webkit-animation: rotating 30s linear infinite;
                }
                @-webkit-keyframes rotating {
                    from{
                        -webkit-transform: rotate(0deg);
                    }
                    to{
                        -webkit-transform: rotate(360deg);
                    }
                }
            }
        }
        @media (min-width:1921px){
            .smartService{
                background: url('../images/project/beautyPoint/beautypoint-con-03-bg.png') 0 -28rem no-repeat;
                background-size: cover;
            }
            .smartService .inner{
                padding: 0 0 0 14.375rem;
            }
            .shape1{
                display:none;
            }
            .smartService li:last-child{
                margin-top: 12rem;
                padding-bottom: 10rem;
            }
        }
    }
}

@media (max-width: 1760px) {
    .beautyPoint{
        .cont2{
            .moArea{
                > div{
                    width: 34.53%;
                    margin-left: 6.4% !important;
                    &:last-child{
                        width: 11.64%;
                    }
                }
            }
            h4{
                position: static;
                width: 100%;
                text-align: center;
                p{
                    margin: 20px 0 50px;
                }
                
            }
        }
        .cont3{
            .smartService{
                li{
                    h4{
                        padding-left: 0 !important;
                    }
                    &:first-child{
                        h4{
                            display: none;
                        }
                    }
                }
            }
        }
    }
}

.BeautyPointMo{
    width: 100%;
    text-align: center;
    .visual{
        *{
            color: #fff;
        }
        position: relative;
        padding: 50px 0;
        background:linear-gradient(to right, #99d7ff, #fda4bd 50%, #fcd49f);
        .inner{
            background:url('../images/project/beautyPoint/mo-beautypoint-ci.png') center 23% no-repeat;
            background-size: 320px;
        }
        .img{
            display: inline-block;
            margin: 50px 0;
            box-shadow: 50px 20px 110px rgba(0, 0, 0, 0.15);
            border-radius: 20px;
            > img{
                width: 138px;
            }
        }
        h2{
            span{
                font-weight: 100;
            }
            p{
                font-size: .975rem
            }
        }
        .prInfo{
            padding: 20px 40px 0;
            text-align: left;
            font-size: .78rem;
            li{
                @include clear;
                width: 100%;
                padding-top: 10px;
                p{
                    float:left;
                    width: 70%;
                    &:first-child{
                        width: 30%
                    }
                }
                &:first-child{
                    padding-top: 0;
                }
                &:last-child{
                    padding-top: 40px;
                    p{
                        width: 100%;
                    }
                }
            }
        }
    }
    .grFont{
        padding: 50px 40px 0;
    }
    div[class^="cont"]{
        margin-top: 100px;
        h3{
            padding: 0 40px;
            font-size: 1.82rem;
            letter-spacing: -1px;
            p{
                padding-top: 10px;
                font-size: .845rem;
            }
        }
        h4{
            padding:50px 50px 0;
            font-size: 1rem;
            font-weight: 500;
            p{
                padding-top: 10px;
                font-size: .78rem;
                font-weight: 100;
            }
        }
    }
    .cont1{
        .moArea{
            background: url('../images/project/beautyPoint/mo-beautypoint-con-01-shape.png') center no-repeat;
            background-size: contain;
            @include clear;
            > div{
                float: left;
                width:33.33%;
                margin-left: 5%;
                &:nth-child(2){
                    padding-top: 40px;
                    margin-left: -5%;
                }
                &:nth-child(3){
                    padding-top: 80px;
                    margin-left: -5%;
                }
            }
        }
    }
    .cont2{
        .inner{
            background: url('../images/project/beautyPoint/mo-beautypoint-con-02-shape.png') center 85% no-repeat;
            background-size: contain;
        }
        .moArea{
            padding: 25px 0;
        }
        .swiper-scrollbar{
            .swiper-scrollbar-drag{
                background: #000000;    
            }
            left: 3%;
            width: 94%;
            height: 3px;
            background: #f4f4f4;
        }
    }
    .cont3{
        .smartService{
            margin-top: 30px;
            background: url('../images/project/beautyPoint/mo-beautypoint-con-03-bg.png') center 0 no-repeat;
            background-size: contain;
            li{
                position: relative;
                &:first-child{
                    color: #fff;
                    .moArea{
                        position: relative;
                        height: 330px;
                        > div{
                            position: absolute;
                            top: 17%;
                            left: 50%;
                            margin-left: -114px;
                            &:first-child{
                                top: 10%;
                                margin-left: -35px;
                                z-index: 1;
                            }
                            
                        }
                    }
                }
                &:last-child{
                    background: url('../images/project/beautyPoint/mo-beautypoint-con-03-shape-04.png') center bottom no-repeat;
                    background-size: contain;
                }
            }
            div[class^="shape"]{
                position: absolute;
            }
            .shape1{
                right: 0;
                bottom: -10%;
            }
            .shape2{
                left: 0;
                bottom: -4.5%;
            }
        }
    }   
}